.dyb-layout {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.dyb-layout.ant-layout,
.dyb-layout .ant-layout {
  background: none;
}
.dyb-layout-sider {
  height: 100%;
}
.dyb-layout-sider .ant-layout-sider-children {
  display: flex;
  flex-direction: column;
}
.dyb-layout-sider .ant-layout-sider-children .ant-menu-root {
  flex: 1 1;
  overflow: hidden auto;
  padding-bottom: 30px;
}
.dyb-layout-sider .ant-layout-sider-children .ant-menu-root::-webkit-scrollbar {
  width: 3px;
  height: 3px;
}
.dyb-layout-sider .ant-layout-sider-children .ant-menu-root::-webkit-scrollbar-thumb {
  background: #a5a5a5;
}
.dyb-layout-sider .ant-layout-sider-children .ant-menu-root::-webkit-scrollbar-track {
  background: #e8e8e8;
}
.dyb-drawer .ant-drawer-body {
  padding: 0;
  height: 100%;
}
.dyb-layout-logo {
  height: 32px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px;
}
.dyb-layout-header {
  font-size: 14px;
  background: #fff !important;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 24px !important;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
.dyb-layout-content {
  overflow: auto;
  position: relative;
}
.dyb-layout-box {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
}
.dyb-layout-box .ant-spin {
  max-height: none !important;
}
.dyb-layout-pageHeader {
  min-height: 64px;
  background-color: #fff;
}
.dyb-form {
  display: flex;
  flex-wrap: wrap;
}
.dyb-form.ant-form-inline .ant-select,
.dyb-form.ant-form-inline .ant-cascader-picker {
  width: 150px;
}
.dyb-form .ant-form-item-children > button:not(:first-of-type) {
  margin-left: 10px;
}
.dyb-form .ant-checkbox-group-item {
  line-height: 39px;
}
.dyb-pagination {
  display: flex;
  justify-content: flex-end;
}
.dyb-textArea {
  padding-top: 4px;
}
.dyb-textArea p {
  text-align: right;
  font-size: 12px;
  line-height: 1;
  padding-right: 3px;
}
.dyb-inputNumber {
  display: inline-block;
}
.dyb-inputNumber .dyb-unit {
  margin-left: 10px;
}
.dyb-input-multiline-item {
  display: flex;
}
.dyb-input-multiline-item > div:first-child {
  flex: 1 1;
}
.dyb-long-text {
  position: relative;
  height: 1.5em;
  cursor: pointer;
}
.dyb-long-text div {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
